import { Component } from '@angular/core';
import { InvoicesService } from './invoices-service.service';
import { Invoice } from './invoice';
import { Lineitems } from './lineitems';
import { NgForm } from '@angular/forms';
import { Message } from 'primeng/components/common/api';
import { isDefined } from '@angular/compiler/src/util';
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'app';

  Invoices: Invoice = new Invoice();
  lineItems: Lineitems = new Lineitems();

  InvoicesLists: Array<any> = [];
  displayDialog: boolean;
  displayItmeDialog: boolean;

  items: Array<any> = new Array();

  msgs: Message[] = [];

  constructor(private InvoicesService: InvoicesService) { }



  getAllInvoices() {
    this.InvoicesLists = [];
    this.InvoicesService.getAllInvoices().subscribe(
      res => { this.InvoicesLists = res },
      err => console.error(err),
    );
  }

  getInvoices() {
    this.InvoicesLists = [];
    this.InvoicesService.getInvoice(this.Invoices.id).subscribe(
      res => { this.InvoicesLists.push(res)
        this.Invoices.id = null;
      },
      err => console.error(err),
    );
  }

  showDialog() {

    this.clear() 
    this.displayDialog = true;
  }

  showLineItmesDialog() {

    this.displayItmeDialog = true;
  }

  addItems(itemsDetails: NgForm) {

    if(!this.Invoices.lineItem){
      this.Invoices.lineItem = [];
    }


    this.Invoices.lineItem.push(itemsDetails.value);

    itemsDetails.resetForm();

    this.items = this.Invoices.lineItem;

    this.displayItmeDialog = false;
  }

  savaInvoice() {
    this.InvoicesService.SaveInvoice(this.Invoices).subscribe(res => {
      this.showSuccess();
      this.clear();
      this.displayDialog= false;
    },
      err => console.error(err))
  }

  showSuccess() {
    this.msgs = [];
    this.msgs.push({ severity: 'success', summary: 'Success Message', detail: 'Invioce submitted' });
  }

  clear() {
    this.Invoices = new Invoice();
    this.items = new Array();

  }

  viewInvoices(InvoicesList){

    this.Invoices = InvoicesList;
    
    this.items = this.Invoices.lineItem;
    this.displayDialog = true;
  }
}



